.fav-movie {
    margin-bottom: 15px;
    figure {
        position: relative;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin: auto;
        width: 100%;
        img {
            height: 400px;
            margin:auto;
        }
    }
    button {
        display: block;
        height: 400px;
        margin: auto;
    }
    ficaption {
        text-align: center;
    }
}

.card {
    margin-bottom: 15px;
}